<template>
  <div>
    <span class="cont"></span>
    <img class="head-title" src="../static/img/result.png">
    <ul>
      <li class="list-item" v-for="(rate, index) in orderedScore" v-if="index < 4">
        <i>{{index + 1}}</i>
        <router-link to="">{{rate.title}}</router-link>
        <el-rate v-model="orderedScore[index].score" :colors="['#99A9BF', '#F7BA2A', '#FF9900']" disabled></el-rate>
      </li>
    </ul>
    <a href="http://cn.mikecrm.com/ua4VoZw" class="btn btn-submit margin-item">前往报名</a>
  </div>
</template>

<script>
  export default {
    methods: {
      compare (property) {
        return function (a, b) {
          var value1 = a[property]
          var value2 = b[property]
          return value2 - value1
        }
      }
    },
    computed: {
      orderedScore () {
        return this.$route.params.rate.sort(this.compare('score'))
      }
    }
  }
</script>


