<template>
  <div>
    <img class="head-title" src="../static/img/ceyice.png">
    <img class="person" src="../static/img/person.svg"/>
    <radio-list @ien="getdata" :question="question" v-for="question in questions" :key="question.id"></radio-list>
    <span v-show="error.state" class="error">请填写第 {{error.message}} 道题！</span>
    <a class="btn btn-submit" @click="calcScore">提 交</a>
  </div>
</template>

<script>
  import RadioList from './components/RadioList'
  import questiondata from '../static/questions.json'

  export default {
    data () {
      return {
        questions: questiondata,
        value: [],
        error: {
          state: false,
          message: ''
        },
        scores: [
          {
            title: '醉晚亭工作室',
            score: 0
          },
          {
            title: '喻园网视',
            score: 0
          },
          {
            title: '华青报社',
            score: 0
          },
          {
            title: '新媒体',
            score: 0
          },
          {
            title: '人力资源部',
            score: 0
          },
          {
            title: '外事信息中心',
            score: 0
          },
          {
            title: '破晓',
            score: 0
          }
        ]
      }
    },
    components: {
      RadioList
    },
    methods: {
      getdata (evtValue, id) {
        let flag = 0
        for (let answer of this.value) {
          if (id === answer.id) {
            answer.id = id
            answer.value = evtValue
            flag = 1
          }
        }
        if (flag === 0) {
          this.value.push({'id': id, 'value': evtValue})
        }
      },
      calcScore () {
        if (this.value.length === 5) {
          for (var item of this.value) {
            switch (item.id) {
              case 1:
                if (item.value === 1) {
                  this.scores[0].score = this.scores[0].score + 1
                  this.scores[1].score = this.scores[1].score + 1
                  this.scores[2].score = this.scores[2].score + 1
                  this.scores[3].score = this.scores[3].score + 1
                  this.scores[6].score = this.scores[6].score + 1
                } else if (item.value === 2) {
                  this.scores[4].score = this.scores[4].score + 1
                  this.scores[5].score = this.scores[5].score + 1
                } else if (item.value === 3) {
                  this.scores[0].score = this.scores[0].score + 1
                  this.scores[6].score = this.scores[6].score + 1
                }
                break
              case 2:
                if (item.value === 1) {
                  this.scores[5].score = this.scores[5].score + 2
                } else if (item.value === 2) {
                  this.scores[4].score = this.scores[4].score + 2
                } else if (item.value === 3) {
                  this.scores[0].score = this.scores[0].score + 1
                  this.scores[1].score = this.scores[1].score + 1
                  this.scores[2].score = this.scores[2].score + 1
                  this.scores[3].score = this.scores[3].score + 1
                }
                break
              case 3:
                if (item.value === 1) {
                  this.scores[2].score = this.scores[2].score + 1
                  this.scores[6].score = this.scores[6].score + 1
                } else if (item.value === 2) {
                  this.scores[3].score = this.scores[3].score + 1
                } else if (item.value === 3) {
                  this.scores[0].score = this.scores[0].score + 1
                } else if (item.value === 4) {
                  this.scores[1].score = this.scores[1].score + 1
                } else if (item.value === 5) {
                  this.scores[4].score = this.scores[4].score + 1
                  this.scores[5].score = this.scores[5].score + 1
                }
                break
              case 4:
                if (item.value === 1) {
                  this.scores[2].score = this.scores[2].score + 1
                  this.scores[3].score = this.scores[3].score + 1
                  this.scores[6].score = this.scores[6].score + 1
                } else if (item.value === 2) {
                  this.scores[6].score = this.scores[6].score + 1
                } else if (item.value === 3) {
                  this.scores[0].score = this.scores[0].score + 1
                } else if (item.value === 4) {
                  this.scores[1].score = this.scores[1].score + 1
                }
                break
              case 5:
                if (item.value === 1) {
                  this.scores[2].score = this.scores[2].score + 1
                } else if (item.value === 2) {
                  this.scores[4].score = this.scores[4].score + 1
                  this.scores[5].score = this.scores[5].score + 1
                } else if (item.value === 3) {
                  this.scores[0].score = this.scores[0].score + 1
                } else if (item.value === 4) {
                  this.scores[1].score = this.scores[1].score + 1
                } else if (item.value === 5) {
                  this.scores[6].score = this.scores[6].score + 1
                }
                break
            }
          }
          var _this = this
          setTimeout(function () {
            _this.$router.push({name: 'Result', params: {rate: _this.scores}})
          }, 1000)
        } else {
          this.error.state = true
          for (let i = 1; i <= 5; i++) {
            let flag = 1
            for (let item of this.value) {
              if (item.id === i) {
                flag = 0
                break
              }
            }
            if (flag === 1) {
              this.error.message = i
            }
          }
        }
      }
    }
  }
</script>


