<template>
<div class="radiolist-wrapper">
  <h4 class="radiolist-title">Q{{question.id}} : {{ question.title }}</h4>
  <div class="radio" v-for="(option, index) in question.options">
    <label>
      <input type="radio" :name="'option' + question.id" :value="index + 1" v-model="picked" @click="iclick">{{ option }}
    </label>
  </div>
</div>
</template>

<script>
export default {
  name: 'radiolist',
  data () {
    return {
      picked: ''
    }
  },
  computed: {
    id () {
      return this.question.id
    }
  },
  methods: {
    iclick () {
      this.$emit('ien', this.picked, this.id)
    }
  },
  props: [
    'question'
  ]
}
</script>


